import React from "react";
import { useStaticQuery, graphql, Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

export default function CaseStudyCards() {
  const data = useStaticQuery(graphql`
    query StudyQuery {
      allMarkdownRemark(
        filter: { frontmatter: { isReady: { eq: true } }, fileAbsolutePath: {} }
        sort: { frontmatter: { date: DESC } }
      ) {
        nodes {
          frontmatter {
            date
            has_password
            isReady
            path
            title
            project_excerpt
            cover_image {
              childImageSharp {
                gatsbyImageData
                fluid {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    }
  `);

  //375

  const studies = data.allMarkdownRemark.nodes;
  // console.log(studies);

//   useEffect(() => {
//     const handleContextmenu = (e) => {
//       e.preventDefault();
//     };
//     document.addEventListener("contextmenu", handleContextmenu);
//     return function cleanup() {
//       document.removeEventListener("contextmenu", handleContextmenu);
//     };
//   }, []);

  function mapColumns() {
    // let left_column = [];
    // let right_column = [];
    // studies.map(function (art, i) {
    //   if (i % 2) {
    //     right_column.push(art);
    //     console.log(art.frontmatter.title);
    //   } else {
    //     left_column.push(art);
    //     console.log(art.frontmatter.title);
    //   }
    // });
    return (
      <div className="case-studies-grid">
        
            
            {studies.map(function (item, i) {
                console.log(item.frontmatter.title);
                const reCoverImg = getImage(
                  item.frontmatter.cover_image?.childImageSharp?.gatsbyImageData
                );
                const title = item.frontmatter.title;
                const path = item.frontmatter.path;
                const excerpt = item.frontmatter.project_excerpt
                const side = i % 2 ? 'right' : 'left'
                return (
                    <div className={`list-item ${side}`}>
                        <Link className="case-study-link" to={path} target="_blank">
                            <div className="img-container">
                                {/* <div className="overlay"></div> */}
                                {/* min-height: 100px; */}
                                <GatsbyImage
                                    fluid={item.frontmatter.cover_image.childImageSharp.fluid}
                                    image={reCoverImg}
                                    data-pin-no-hover="true"
                                    nopin="nopin"
                                />
                            </div>
                            <div className="card-text-container">
                                <h2 className="case-study-title">{title}</h2>
                                <div className="case-study-excerpt-details">
                                  <p>{excerpt}</p>
                                </div>
                                
                            </div>
                        </Link>
                    </div>
                );
            })}
            
      </div>
    );
  }

  return (
    <div className="case-studies-list" aria-label="case studies">
      {mapColumns()}
    </div>
  );
}
